import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {CustomValidators} from 'ng2-validation';
import {Banque} from "../metier/banque.metier";
import {BanqueService} from "../service/banque.service";
import {BaseComponent} from "../../../core/component/base-component";
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {PageTitleService} from "../../../core/page-title/page-title.service";

const password = new FormControl('', Validators.required);
const confirmPassword = new FormControl('', CustomValidators.equalTo(password));

@Component({
  selector: 'ms-banque-add',
  templateUrl: './banque.add.component.html',
  styleUrls: ['./banque.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class BanqueAddComponent extends BaseComponent implements OnInit {

  public banqueForm:FormGroup;
  private banque:Banque;

  constructor(private fb:FormBuilder, private pageTitleService:PageTitleService, private banqueService:BanqueService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UNE BANQUE");
    this.banqueForm = this.fb.group({
      libelle: ['', Validators.required],
      abreviation: ['', Validators.required],
    });
  }

  saveBanque(banqueForm:any) {
    this.banque = new Banque(banqueForm);
    this.banqueService.saveBanque(this.banque)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(["/admin/banque/list"]);
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  reset() {
    this.banqueForm.reset();
  }


}






