import {Routes} from '@angular/router';

import {BanqueAddComponent}  from './banque.add/banque.add.component';
import {BanqueListComponent} from "./banque.list/banque.list.component";
import {BanqueEditComponent} from "./banque.edit/banque.edit.component";

export const BanqueRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: BanqueAddComponent
  },
    {path: 'list', component: BanqueListComponent},
    {path: 'edit/:id', component: BanqueEditComponent},
  ]
}];



