import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {BanqueAddComponent} from './banque.add/banque.add.component';
import {BanqueRoutes} from './banque.routing';
import {BanqueService} from "./service/banque.service";
import {BanqueListComponent} from "./banque.list/banque.list.component";
import {BanqueEditComponent} from "./banque.edit/banque.edit.component";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(BanqueRoutes)
  ],
  declarations: [
    BanqueAddComponent,
    BanqueListComponent,
    BanqueEditComponent,
  ],
  providers: [
    BanqueService,
  ],
})

export class BanqueModule {
}



