import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Banque} from "../metier/banque.metier";
import {BanqueService} from "../service/banque.service";
import {BaseComponent} from "../../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";

@Component({
  selector: 'ms-banque-edit',
  templateUrl: './banque.edit.component.html',
  styleUrls: ['./banque.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class BanqueEditComponent extends BaseComponent implements OnInit {
  private banque:Banque;
  public banqueForm:FormGroup;


  constructor(private fb:FormBuilder,
              private route:ActivatedRoute,
              private pageTitleService:PageTitleService,
              private banqueService:BanqueService) {
    super();
  }

  ngOnInit() {
    this.loadBanque();
    this.pageTitleService.setTitle("MODIFIER UNE BANQUE");
    this.banqueForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],
      abreviation: ['', Validators.required],

    });
  }


  updateBanque(banqueForm:any) {
    this.banque = new Banque(banqueForm);
    this.banqueService.saveBanque(this.banque)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/banque/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadBanque() {
    this.route.params.switchMap((params:Params) => this.banqueService.getBanque(+params['id']))
      .subscribe(banque => {
          this.banque = banque;
          this.banqueForm.patchValue(this.banque);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteBanque(banqueForm:any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.banque = new Banque(banqueForm);
    this.banqueService.deleteBanque(this.banque)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/banque/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }


}






