"use strict";

import {Component,  OnInit, ViewEncapsulation} from '@angular/core';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Banque} from "../metier/banque.metier";
import {FormGroup, FormBuilder} from "@angular/forms";
import {BaseComponent} from "../../../core/component/base-component";
import {BanqueService} from "../service/banque.service";
import {PageTitleService} from "../../../core/page-title/page-title.service";

@Component({
  selector: 'ms-banque-list',
  templateUrl: './banque.list.component.html',
  styleUrls: ['./banque.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class BanqueListComponent extends BaseComponent implements OnInit {

  public banqueForm:FormGroup;
  private banque:Banque;
  private banqueList:Array<Banque> = [];

  constructor(private fb:FormBuilder,
              private banqueService:BanqueService,
              private pageTitleService:PageTitleService,) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES BANQUES");
    this.loadBanqueList();
  }

  createForm() {
    this.banqueForm = this.fb.group({
      libelle: '',
      abreviation: ''
    });
  }


  findBanqueList(banqueForm:any) {
    this.banque = new Banque(banqueForm);
    this.banqueService.findBanqueListByCriteria(this.banque)
      .subscribe(banqueList => {
        this.banqueList = banqueList;
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private loadBanqueList() {
    this.banqueService.getBanqueList()
      .subscribe(banqueList => {
          this.banqueList = banqueList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }


}






